@mixin page-h1() {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    line-height: 58px;
    
    /* темный/фон */
    color: $dark-bg;

    @media screen and (max-width: 1110px) {
        font-weight: 800;
        font-size: 32px;
        line-height: 39px;        
    }
}
@mixin page-h2() {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    line-height: 51px;
    
    /* identical to box height */
    
    /* светлый/Текст */
    color: $light-text-1;

    @media screen and (max-width: 1110px) {
        font-weight: 800;
        font-size: 32px;
        line-height: 39px;       
    }
}
@mixin page-h3() {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    
    /* identical to box height */
    
    /* светлый/Текст */
    color: $white;

    @media screen and (max-width: 1110px) {
        font-weight: 800;
        font-size: 16px;
        line-height: 22px;       
    }
}

@mixin page-text-1() {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    
    color: rgba(255, 255, 255, 0.7);

    @media screen and (max-width: 1110px) {
        font-size: 14px;
        line-height: 19px;     
    }
}

@mixin page-bordered-span($color) {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    
    padding: 3px 8px;
    box-sizing: border-box;
    border: 1px solid $color;
    border-radius: 100px;  
    text-decoration: none; 
    color: $light-text-1;  
}

@mixin page-link() {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    /* identical to box height */
    text-decoration: underline;
    
    /* темный/акцент */
    color: $light-orange;

    @media screen and (max-width: 1110px) {
        font-size: 14px;
        line-height: 19px;     
    }
}


@mixin page-btn() {
    cursor: pointer;
    outline: none;
    border: none;
    padding: 10px 62px;
    box-sizing: border-box;
    background: $light-orange;
    border: 1px solid $light-orange;
    border-radius: 10px;
    text-decoration: none;

    font-family: 'Mulish';
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 28px;
    color: #FFFFFF; 
    display: inline-block;
    
    &.maxwidth {
        width: 100%;
    }
    &.moreradius {
        border-radius: 100px;        
    }
    &.transparent {
        background: transparent;
        color: $light-orange;
    }
}
