@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: #fff;
}
.header,
.footer {
    flex-shrink: 0;
}
.main {
    flex-grow: 1;
}
.container {
    max-width: 1170px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
}
.page-h1 {
    @include page-h1();
}
.page-h2 {
    @include page-h2();
}
.page-h3 {
    @include page-h3();
}
.page-link {
    @include page-link();
}
.page-btn {
    @include page-btn();
}
.page-text-1 {
    @include page-text-1();
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    padding-top: 20px;
    z-index: 100;
    padding-bottom: 20px;
    background: #fff;

    .container {

    }
    .header__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .header__burger-wrapper {

    }
    .header__burger {
        position: relative;
        height: 24px;
        width: 45px;
        box-sizing: border-box;

        display: flex;
        justify-content: start;
        align-items: center;

        &._active {
            .header__burger-item {
                opacity: 0;
            }
            &::before,
            &::after {
                transform: translateY(-50%) rotate(45deg);
                bottom: unset;
                top: 50%;
            }
            &::after {
                transform: translateY(-50%) rotate(-45deg);
            }
        }

        &::before,
        &::after {
            content: '';
            width: 100%;
            display: block;
            position: absolute;
            left: 0;
            height: 3px;
            width: 100%;
            background: $light-orange; 
            transition: .3s;
        }
        &::before {
            top: 0;
        }
        &::after {
            bottom: 0;
        }
    }
    .header__burger-item {
        transition: .3s;
        height: 3px;
        width: 80%;
        background: $light-orange;    
    }
    .header__links {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__btn {
        margin-left: 64px;
    }
}

.header__socials {
    display: flex;
    justify-content: start;
    align-items: center;
}
.header__social {
    &:not(:last-child) {
        margin-right: 20px;
    }
    &:hover {
        a {
            color: #242424;
        }
    }
    a {
        color: #727272;
        display: block;
        width: 20px;
        height: 20px;
    }
    svg {
        width: 100%;
        height: 100%;
    }
}

.menu {
    transform: translateX(-200vw);
    visibility: hidden;
    transition: .7s;

    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #fff;
    z-index: 90;

    padding-top: 204px;
    box-sizing: border-box;

    &._active {
        transform: translateX(0);
        visibility: visible;
    }
    .container {

    }
    .menu__wrapper {

    }
    .menu__body {
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 150px;
    }
    .menu__nav {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 90px;
    }
    .menu__list {
        &.--mini {
            .menu__item {
                font-weight: 400;
                font-size: 22px;
                line-height: 27px;
                
                /* identical to box height */
                
                /* base text */
                color: #9C9C9C;   
                a {
                    font: inherit;
                    color: inherit;
                }             
            }
        }
    }
    .menu__item {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 32px;
        line-height: 39px;
        
        /* identical to box height */
        
        /* светлый/Текст */
        color: $light-text-1;
        a {
            font: inherit;
        }

        &:not(:last-child) {
            margin-bottom: 43px;
        }
    }
    .menu__img {
        img {

        }
    }

    .menu__footer {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .menu__footer-wrap {
        margin-right: 80px;
        display: flex;
        flex-direction: column;
    }
    .menu__footer-item {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
        span {
            font-family: 'Mulish';
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 23px;
            
            /* base text */
            color: #9C9C9C;
        }
        a {
            font-family: 'Mulish';
            font-style: normal;
            font-weight: 500;
            font-size: 22px;
            line-height: 23px;
            
            /* base text */
            color: #141518;
            &.--orange {
                color: $light-orange;
            }
        }
    }
}

.title {
    padding-top: 281px;
    .container {
        position: relative;

    }
    .title__wrapper {
    }
    .title__left {
        max-width: 560px;
        width: 100%;
    }
    .title__header {
        // font-family: 'Inter';
        // font-style: normal;
        // font-weight: 800;
        // font-size: 48px;
        // line-height: 58px;
        // color: $dark-bg;  
        margin-bottom: 20px;   
        
        @include page-h1();
    }
    .title__desc {
        font-family: 'Mulish';
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 28px;
        /* темный/текст 2 */
        color: $dark-text;      
        margin: 20px 0;
    }
    .title__link {

    }

    .title__list {
        margin-top: 68px;

        display: flex;
        justify-content: start;
        align-items: flex-start;
    }
    .title__item {
        &:not(:last-child) {
            margin-right: 30px;
        }
        span {
            display: block;
            font-family: 'Inter';
            font-style: normal;
            font-weight: 800;
            font-size: 42px;
            line-height: 51px;
            color: $light-orange;
            
            opacity: 0.3; 
            margin-bottom: 15px;           
        }
        p {
            font-family: 'Mulish';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            
            /* светлый/Текст 2 */
            color: $light-text-2;
        }
    }
    .title__right {
        position: absolute;
        top: -115px;
        right: -100px;
        width: 55%;
        img {
            width: 100%;
        }
    }
}

.portfolio {
    padding-top: 160px;
    padding-bottom: 70px;

    .container {

    }
    .portfolio__wrapper {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .portfolio__headline {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 50px;
    }
    .portfolio__header {

    }
    .portfolio__link {
    }
    .portfolio__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-auto-columns: 1fr;
        gap: 20px 20px;
        grid-auto-flow: row;
        grid-template-areas:
        "col1 col1 col1 col1 col2 col2" 
        "col1 col1 col1 col1 col2 col2" 
        "col1 col1 col1 col1 col2 col2" 
        "col1 col1 col1 col1 col4 col4" 
        "col3 col3 col3 col3 col4 col4" 
        "col3 col3 col3 col3 col4 col4" 
        "col5 col5 col5 col6 col6 col6" 
        "col5 col5 col5 col6 col6 col6" 
        "col5 col5 col5 col6 col6 col6";
    }
    .col1 { grid-area: col1;}
    .col2 { grid-area: col2;}
    .col3 { grid-area: col3;}
    .col4 { grid-area: col4;}
    .col5 { grid-area: col5;}
    .col6 { grid-area: col6;}

    .portfolio__item {
        position: relative;
        box-sizing: border-box;
        padding: 58px 36px;
        overflow: hidden;
        border-radius: 10px;

        

        &.--text-between {
            a {
                justify-content: space-between;
            }
        }
        a {
            text-decoration: none;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }
    }
    .portfolio__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        scale: 1.2;
        z-index: 0;
    }
    .portfolio__logo {
        position: relative;
        z-index: 1;
        width: auto;
        height: auto;
        object-fit: contain;

        // position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
    }
    .portfolio__item-headline {
        position: relative;  
        z-index: 1;
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
    .portfolio__item-logo {
        
    }
    .portfolio__item-circle {
        width: 60px;
        height: 60px;
        border: 2px solid #EE7846;
        box-sizing: border-box;
        border-radius: 50%;
    }
    .portfolio__item-info {
        position: relative;  
        z-index: 1;     
    }
    .portfolio__item-date {
        @include page-bordered-span($light-orange);
        color: $white;
    }
    .portfolio__item-name {
        margin: 28px 0 8px;
    }
    .portfolio__item-desc {

    }
    .portfolio__link-more {
        margin: 50px auto 0;
    }
}

.what_to_expect .banner-text h3 {
    color: $light-text-1;
}
.prices {
    .prices-item {
        h2 {
            font-family: 'Manrope';
            font-style: normal;
            font-weight: 800;
            font-size: 52px;
            line-height: 181.52%;

            /* or 94px */

            /* светлый/Текст 2 */
            color: #414447;
        }
        h3 {
            color: $light-text-2;
        }
        li {
            color: $light-text-2;
            span {
                font-weight: 700;
                margin-left: 20px;
            }
        }
        .prices__text {
            font-family: 'Manrope';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            
            /* светлый/Текст 2 */
            color: $light-text-2;            
        }
        .prices__wrap {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
            span {
                display: block;
                font-family: 'Manrope';
                font-style: normal;
                font-weight: 700;
                font-size: 24px;
                line-height: 32px;
                
                /* светлый/Текст 2 */
                color: $light-text-2; 
            }
        }
    }
}

.footer {
    .container {

    }
    .footer__wrapper {

    }
    .footer__top {
        padding-top: 35px;
        padding-bottom: 35px;
        border-top: 1px solid $light-disabled;

        .container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    .footer__top-wrap {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .footer__logo {
        display: block;
        width: 56px;
        height: 61px;
        color: $light-orange;

        svg {
            width: 100%;
            height: 100%;
        }
    }
    .footer__nav {
        margin-left: 53px;
    }
    .footer__list {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .footer__item {
        &:not(:last-child) {
            margin-right: 40px;
        }
        a {
            text-decoration: none;
            font-family: 'Manrope';
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 25px;  
            color: $light-text-2;          
        }
    }
    .footer__top-wrap {

    }
    .footer__lang {
        font-family: 'Manrope';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 19px;

        padding: 3px 8px;
        box-sizing: border-box;
        border: 1px solid $light-disabled;
        border-radius: 100px;  
        text-decoration: none; 
        color: $light-text-1;  
        
        &:not(:last-child) {
            margin-right: 10px;
        }
        &._active {
            border-color: $light-orange;
        }
    }

    .footer__bot {
        padding-top: 32px;
        padding-bottom: 32px;
        border-top: 1px solid $light-disabled;

        .container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    .footer__bot-wrap {

    }
    .footer__text {
        font-family: 'Mulish';
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        display: block;
        
        /* светлый/Текст */
        color: $light-text-1;        
        a {
            color: $light-orange;  
            text-decoration: none;
        }
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }
}

@media screen and (max-width: 968px) {
    .footer .footer__bot-wrap {
        margin-bottom: 52px;
    }
    .footer .footer__bot .container {
        flex-direction: column;
        justify-content: start;
        align-items: flex-start;
    }
    .footer .footer__item:not(:last-child) {
        margin-right: 0;
        margin-bottom: 30px;
    }
    .footer .footer__logo {
        margin-bottom: 53px;
    }
    .footer .footer__top .container {
        flex-direction: column;
        justify-content: start;
        align-items: flex-start;
    }
    .footer .footer__top-wrap {
        &:nth-child(1) {
            flex-direction: column;
            align-items: flex-start;
        }
        &:nth-child(2) {
            margin-top: 52px;
        }
    }
    .footer .footer__nav {
        margin-left: 0;
    }
    .footer .footer__list {
        flex-direction: column;
        align-items: flex-start;
    }
    .portfolio .portfolio__list {
        display: block;
    }
    .portfolio .portfolio__item {
        height: 380px;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    .portfolio {
        padding-top: 60px;
    }
    .header .header__socials {
        display: none;
    }
    .title {
        padding-top: 149px;
    }
    .title .title__list {
        margin-top: 430px;
    }
    .title .title__right {
        right: unset;
        top: 285px;
        left: 0;
        height: 323px;
        width: auto;
        img {
            height: 100%;
            object-fit: contain;
        }
    }
    .title .title__list {
        flex-direction: column;
    }
    .title .title__item {
        display: grid;
        grid-template-columns: 1fr 4fr;
        gap: 0 18px;
        align-items: center;
        p {
            max-width: 170px;
        }
    }
    .title .title__item:not(:last-child) {
        margin-right: 0;
        margin-bottom: 50px;
    }
}


